<template>
  <div class="d-flex">
    <!--    <h4>Axes {{ axes }}</h4>-->
    <v-select
      max-width="50"
      class="mr-4"
      v-model="yAxis"
      :items="axes"
      label="Y Axis"
      @change="yAxisChanged($event)"
    ></v-select>

    <v-select
      max-width="50"
      v-model="xAxis"
      :items="axes"
      label="X Axis"
      @change="xAxisChanged($event)"
    ></v-select>
  </div>
</template>

<script>
export default {
  name: "AxesManipulation",
  props: ["axes"],
  data: () => ({
    yAxis: "",
    xAxis: "",
  }),
  methods: {
    initInputs() {
      if (this.axes.length > 1) {
        this.xAxis = this.axes[0];
        this.yAxis = this.axes[1];
      } else if (this.axes.length === 1) {
        this.xAxis = this.axes[0];
        this.yAxis = this.axes[0];
      }
    },
    yAxisChanged() {
      this.$emit("yAxisChanged", this.yAxis);
    },
    xAxisChanged() {
      this.$emit("xAxisChanged", this.xAxis);
    },
  },
  beforeMount() {
    this.initInputs();
  },
};
</script>

<style>
.v-select__selections {
  min-width: 150px;
}
.v-select__selections input {
  display: none;
}
</style>
