<template>
  <div>
    <!--    debug {{ data[0] }}-->
    <!--    debug {{ chartInstance }}-->
    <!--    {{ data }}-->
    <!--    debug {{ xAxisLabel }}-->
    <div :id="chartId"></div>
  </div>
</template>

<script>
import Highcharts from "highcharts";
export default {
  props: ["chartId", "isStatic", "data", "xAxisLabel", "yAxisLabel"],
  data: () => ({
    chartInstance: null,
  }),
  methods: {
    renderPlot() {
      const defaultColor = "#7cb5ec";
      const defaultHighchartsLabelColor = "#666666";
      let that = this;
      this.chartInstance = Highcharts.chart(this.chartId, {
        chart: {
          type: "scatter",
          // zoomType: this.isStatic ? "undefined" : "xy",
          height: this.isStatic ? 150 : 450,
        },

        tooltip: {
          style: {
            pointerEvents: "auto",
          },
          stickOnContact: true,
          useHTML: true,
          formatter: function () {
            let validHref = `https://${this.point.link}`;
            return `<b style="font-size:14px;"><a href="${validHref}" target="_blank">${this.point.name}</a></b><br><div>${this.x} ${that.xAxisLabel}</div><br><div>${this.y} ${that.yAxisLabel}</div>`;
          },
        },

        title: {
          text: null,
        },

        credits: {
          enabled: false,
        },

        subtitle: {
          text: null,
        },

        xAxis: {
          // labels: {
          //   enabled: this.isStatic ? false : true,
          // },
          title: {
            enabled: this.isStatic ? false : true,
            text: this.isStatic ? "A" : this.xAxisLabel,
          },
          gridLineWidth: 2,
          gridLineDashStyle: "ShortDot", // "Dot" might fit as well
          tickLength: 0,
          lineWidth: 0,
          lineColor: "transparent",
          minorGridLineWidth: 0,
          minorTickLength: 0,
        },

        yAxis: {
          // labels: {
          //   enabled: this.isStatic ? false : true,
          // },
          title: {
            text: this.isStatic ? "B" : this.yAxisLabel,
            style: {
              color: this.isStatic
                ? "transparent"
                : defaultHighchartsLabelColor,
            },
          },
          gridLineWidth: 2, // controls all horizontal grid lines
          gridLineDashStyle: "Dot",
          tickLength: 0,
          lineWidth: 0,
          lineColor: "transparent",
          minorGridLineWidth: 0,
          minorTickLength: 0,
        },

        legend: {
          enabled: false,
          layout: "vertical",
          align: "left",
          verticalAlign: "top",
          x: 100,
          y: 70,
          floating: true,
          backgroundColor: Highcharts.defaultOptions.chart.backgroundColor,
          borderWidth: 1,
        },

        plotOptions: {
          scatter: {
            marker: {
              radius: 5,
              states: {
                hover: {
                  enabled: true,
                  lineColor: "rgb(100,100,100)",
                },
              },
            },
            states: {
              hover: {
                marker: {
                  enabled: false,
                },
              },
            },
            // tooltip: {
            //   headerFormat: "<b>{point.name}</b><br>",
            //   pointFormat: `<div>{point.x} ${this.xAxisLabel}</div><br><div>{point.y} ${this.yAxisLabel}</div>`,
            // },
          },
        },

        series: [
          {
            name: "Product",
            color: defaultColor,
            data: this.data,
          },
        ],
      });
    },
    updateDynamicScatterplotData() {
      this.chartInstance.update(
        {
          series: [
            {
              data: this.data,
            },
          ],
        },
        true,
        true
      );
    },
    updateStaticScatterplotData() {
      // let isActiveCount = 0;
      // this.data.forEach((p) => {
      //   // console.log("p isActive ", p.isActive);
      //   if (p.isActive) {
      //     isActiveCount++;
      //   }
      // });
      // console.log("active ", isActiveCount);
      this.chartInstance.update(
        {
          series: [
            {
              data: this.data,
            },
          ],
        },
        true,
        true
      );
    },
  },
  // TODO - on change of these value use the .update method on chart instance to SMART update what is relevant
  watch: {
    xAxisLabel() {
      this.chartInstance.update({
        series: [
          {
            data: this.data,
          },
        ],
        xAxis: {
          title: {
            text: this.xAxisLabel,
          },
        },
      });
    },
    yAxisLabel() {
      this.chartInstance.update({
        series: [
          {
            data: this.data,
          },
        ],
        yAxis: {
          title: {
            text: this.yAxisLabel,
          },
        },
      });
    },
    data() {
      // render only changes needed - redraw + oneByOne set to true
      if (!this.isStatic) {
        this.updateDynamicScatterplotData();
      }

      if (this.isStatic) {
        this.updateStaticScatterplotData();
      }
    },
  },
  mounted() {
    this.renderPlot();
  },
};
</script>

