<template>
  <div>
    <v-container class="pt-0">
      <v-expansion-panels :value="panels" multiple>
        <v-expansion-panel
          v-for="(filter, i) in filters"
          :key="i"
          @change="panelChanged(i)"
        >
          <v-expansion-panel-header>
            <div class="column">
              <div class="body-1 font-weight-bold">{{ filter.key }}</div>
              <div>
                <span class="body-2 mr-2">Min: {{ filter.absMin }}</span>
                <span class="body-2">Max: {{ filter.absMax }}</span>
              </div>
            </div>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <!--            <span>{{ filter.values }}</span>-->

            <FilterItem
              :filter="filter"
              @activeRangeChanged="activeRangeChanged"
            />
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </v-container>
  </div>
</template>

<script>
import FilterItem from "@/components/FilterItem";
export default {
  name: "Filters",
  props: ["panels", "filters"],
  components: {
    FilterItem,
  },
  methods: {
    panelChanged(i) {
      this.$emit("panelChanged", i);
    },
    activeRangeChanged(filter) {
      this.$emit("activeRangeChanged", filter);
    },
    initAbsMinMaxAndDynamicStepSize() {
      // Given bases 0.1 and 0.5 we find a step size <base value>*Math.pow(10, n)
      // Which gives us approximately n_steps steps
      // for round-looking numbers with a fairly smooth slider action and an inclusive range
      this.filters.forEach((filter) => {
        let n_steps = 1000;
        let exact_step = (filter.max - filter.min) / n_steps;
        let bases = [0.5, 0.1];
        let sample_step = bases[0];
        let steps = (filter.max - filter.min) / sample_step;
        if (steps < n_steps) {
          while (sample_step > exact_step) {
            sample_step = bases[bases.length - 1];
            bases.push(bases[bases.length - 2] / 10.0);
          }
        } else {
          while (sample_step < exact_step) {
            sample_step = bases[bases.length - 1];
            bases.push(bases[bases.length - 2] * 10.0);
          }
        }
        let step = sample_step;
        let lower_limit = Math.floor(filter.min / step) * step;
        lower_limit = parseFloat(lower_limit.toFixed(10));
        if (lower_limit > filter.min) {
          lower_limit = lower_limit - step;
        }
        let upper_limit = Math.ceil(filter.max / step) * step;
        upper_limit = parseFloat(upper_limit.toFixed(10));
        if (upper_limit < filter.max) {
          upper_limit = upper_limit + step;
        }

        filter.stepSize = step;
        filter.absMin = lower_limit;
        filter.absMax = upper_limit;
        filter.currMin = lower_limit;
        filter.currMax = upper_limit;
        filter.range = [lower_limit, upper_limit];
      });
    },
  },
  beforeMount() {
    this.initAbsMinMaxAndDynamicStepSize();
  },
};
</script>
