<template>
  <div>
    <Histogram
      :chartId="setUniqueChartId(filter.key)"
      :data="filter"
      :currMin="currMin"
      :currMax="currMax"
    />

    <!--    debug {{ range }}-->

    <!--    <div>min {{ absMin }}</div>-->
    <!--    <div>max {{ absMax }}</div>-->

    <!--    debug {{ currMin }}-->

    <!--    Debug step size {{ getDynamicStepSize(filter) }}-->

    <!--    debug {{ stepSize }}-->
    <v-row>
      <!--      debug step="0.01" helps handle decimal values-->
      <!--      thumb size defaults to 32 can be changed-->

      <v-range-slider
        :min="filter.absMin"
        :max="filter.absMax"
        :step="filter.stepSize"
        :value="range"
        @change="setActiveRange"
      >
      </v-range-slider>
    </v-row>

    <v-row class="mt-0">
      <v-col class="pt-0">
        <v-text-field
          v-model="currMin"
          class="pt-0"
          label="Min"
          type="number"
          step="any"
          :rules="[
            (value) =>
              value >= filter.absMin ||
              `Min has to be at least ${filter.absMin}`,
          ]"
          @blur="updateMinRange"
          @keyup.enter="updateMinRange"
        ></v-text-field>
      </v-col>
      <v-col class="pt-0">
        <v-text-field
          v-model="currMax"
          class="pt-0"
          label="Max"
          type="number"
          step="any"
          :rules="[
            (value) =>
              value <= filter.absMax ||
              `Max has to be at the most ${filter.absMax}`,
          ]"
          @blur="updateMaxRange"
          @keyup.enter="updateMaxRange"
        ></v-text-field>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import Histogram from "@/components/Histogram";
export default {
  name: "ResultsFilter",
  components: {
    Histogram,
  },
  props: ["filter"],
  data: () => ({
    currMin: null,
    currMax: null,
    range: [],
    isGrabbingSlider: false,
    isFinishedInitFilter: false,
    stepSize: 1,
  }),
  methods: {
    setActiveRange(newRange) {
      // console.log("new range ", newRange);
      this.range = newRange;
      this.currMin = newRange[0];
      this.currMax = newRange[1];
      if (!this.isFinishedInitFilter) return;

      this.$emit("activeRangeChanged", {
        key: this.filter.key,
        activeRange: this.range,
      });
    },
    updateMaxRange() {
      // Vuetify v-range-slider caveat forces to pass a full array with 2 values to detect change.
      this.range = [this.range[0], parseInt(this.currMax)];
      this.$emit("activeRangeChanged", {
        key: this.filter.key,
        activeRange: this.range,
      });
    },
    updateMinRange() {
      // Vuetify v-range-slider caveat forces to pass a full array with 2 values to detect change.
      this.range = [parseInt(this.currMin), this.range[1]];
      this.$emit("activeRangeChanged", {
        key: this.filter.key,
        activeRange: this.range,
      });
    },
    setUniqueChartId(key) {
      const cleanKey = key
        .toLowerCase()
        .replace(" ", "")
        .replace(/[^\w\s]/gi, "");
      return `engora-filter-${cleanKey}`;
    },
    initFilter() {
      this.range = this.filter.range;
      this.currMin = this.filter.currMin;
      this.currMax = this.filter.currMax;
    },
  },
  beforeMount() {
    this.initFilter();
  },
  mounted() {
    this.isFinishedInitFilter = true;
  },
};
</script>

<style lang="scss">
.v-slider__thumb {
  cursor: pointer;
}
</style>
