<template>
  <div>
    <!--    debug {{ chartId }}-->
    <!--    debug {{ data.values }}-->
    <!--    debug {{ chartInstance }}-->

    <!--    <div>debug {{ currMin }}</div>-->
    <!--    <div>debug {{ currMax }}</div>-->
    <div :id="chartId"></div>
  </div>
</template>

<script>
import Highcharts from "highcharts";
import * as histogram from "highcharts/modules/histogram-bellcurve";
histogram(Highcharts);

export default {
  name: "Histogram",
  props: ["chartId", "data", "currMin", "currMax"],
  data: () => ({
    chartInstance: null,
    chartData: [],
    barPoints: [],
  }),
  methods: {
    initHistogramData() {
      if (this.data) {
        this.chartData = this.data.values;
      }
    },
    renderHistogram() {
      // let that = this;
      this.chartInstance = Highcharts.chart(this.chartId, {
        chart: {
          height: 120,
          animation: false,
        },
        title: {
          text: null,
        },

        tooltip: {
          enabled: false, // turn to true to see data points if needed
        },

        xAxis: [
          {
            title: { text: "Data", enabled: false },
            visible: false,
          },
          {
            title: { text: "Histogram", enabled: false },
            visible: false,
          },
        ],

        yAxis: [
          {
            title: { text: "Data", enabled: false },
            visible: false,
          },
          {
            title: { text: "Histogram", enabled: false },
            visible: false,
          },
        ],

        plotOptions: {
          histogram: {
            accessibility: {
              pointDescriptionFormatter: function (point) {
                var ix = point.index + 1,
                  x1 = point.x.toFixed(3),
                  x2 = point.x2.toFixed(3),
                  val = point.y;
                return ix + ". " + x1 + " to " + x2 + ", " + val + ".";
              },
            },
          },
          scatter: {
            visible: false,
          },
          series: {
            // cursor: "pointer",
            // colorByPoint: true,
            states: {
              hover: {
                enabled: false,
              },
            },
          },
        },

        series: [
          {
            name: "Histogram",
            type: "histogram",
            xAxis: 1,
            yAxis: 1,
            baseSeries: "s1",
            zIndex: -1,
          },
          {
            name: "Data",
            type: "scatter",
            data: this.chartData,
            id: "s1",
            marker: {
              radius: 1.5,
            },
          },
        ],

        credits: {
          enabled: false,
        },

        legend: {
          enabled: false,
        },
      });
    },
    mapBarPoints() {
      this.barPoints = [];
      // 0 data series
      this.chartInstance.series[0].points.forEach((p) => {
        if (p.barX) {
          this.barPoints.push(p);
        }
      });
    },
    changeBarColors() {
      let defaultColor = "#7cb5ec";
      let outOfRangeColor = "#DBE2EA";
      this.barPoints.forEach((p) => {
        // p.category holds the bar value in histogram
        let isBarInActiveRange =
          this.currMin <= p.category && p.category <= this.currMax;
        if (isBarInActiveRange) {
          if (p.color !== defaultColor) {
            p.update({ color: defaultColor });
          }
        } else {
          if (p.color !== outOfRangeColor) {
            p.update({ color: outOfRangeColor });
          }
        }
      });
    },
  },
  watch: {
    currMin() {
      this.changeBarColors();
    },
    currMax() {
      this.changeBarColors();
    },
  },
  mounted() {
    // console.log("data values ", this.data.values);
    this.initHistogramData();
    this.renderHistogram();
    this.mapBarPoints();
  },
};
</script>
