<template>
  <v-row>
    <v-col md="3">
      <v-select
        v-model="specType"
        :items="specTypes"
        @change="typeChanged($event)"
      >
        <template v-slot:selection="{ item }">
          <v-icon class="mr-2">{{ getRequirementTypeIcon(item) }}</v-icon>
          {{ item }}
        </template>
        <template v-slot:item="{ item }">
          <v-icon class="mr-2">{{ getRequirementTypeIcon(item) }}</v-icon>
          {{ item }}
        </template>
      </v-select>
    </v-col>
    <v-col md="3">
      <v-text-field
        v-model="specName"
        label="Specification Name"
        @input="specNameInput($event)"
      ></v-text-field>
    </v-col>
    <v-col v-if="shouldShowUnit" md="3">
      <v-select
        v-model="specUnit"
        :items="units"
        label="Unit"
        @change="unitChanged($event)"
      ></v-select>
    </v-col>
    <v-col align-self="center">
      <v-btn icon @click="remove">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-col>
  </v-row>
</template>

<script>
export default {
  name: "Requirement",
  props: ["req", "index"],
  computed: {
    shouldShowUnit() {
      return this.specType === this.specTypes[1];
    },
  },
  data: () => ({
    specTypes: ["category", "numeric"], // has UI support for "non-numeric", backend not yet
    specType: "numeric",
    specUnit: "any",
    specName: "",
    units: [
      { text: "any", value: "any" },
      { text: "force", value: "force" },
      { text: "mass", value: "mass" },
      { text: "angle", value: "angle" },
      { text: "length", value: "length" },
      { text: "time", value: "time" },
      { text: "voltage", value: "voltage" },
      { text: "current", value: "current" },
      { text: "speed", value: "speed" },
      { text: "torque", value: "torque" },
      { text: "angular speed", value: "angular_speed" },
      { text: "current capacity", value: "current_capacity" },
      { text: "spring constant", value: "spring_constant" },
    ],
    reqTypeIconMap: {
      CATEGORY: "shape",
      NUMERIC: "numeric",
      "NON-NUMERIC": "asterisk",
    },
  }),
  methods: {
    remove() {
      this.$emit("remove", this.index);
    },
    prePopulateRequirement() {
      this.specType = this.req.type ? this.req.type : this.specType;
      this.specName = this.req.value ? this.req.value : this.specName;
      this.specUnit = this.req.unit ? this.req.unit : this.specUnit;
    },
    typeChanged() {
      this.$emit("typeChange", {
        index: this.index,
        type: this.specType,
      });
    },
    unitChanged() {
      this.$emit("unitChange", {
        index: this.index,
        unit: this.specUnit,
      });
    },
    specNameInput() {
      this.$emit("specNameInput", {
        index: this.index,
        value: this.specName,
      });
    },
    getRequirementTypeIcon(reqType) {
      return `mdi-${this.reqTypeIconMap[reqType.toUpperCase()]}`;
    },
  },
  beforeMount() {
    this.prePopulateRequirement();
  },
};
</script>
