<template>
  <v-expand-x-transition>
    <v-card class="mx-auto elevation-1 ml-2" height="220">
      <v-card-actions class="pt-0 pl-2 pr-2">
        <v-text-field
          v-model="header"
          :rules="[noFirstDigitRule]"
          label="Column"
          class="shrink mr-2"
        ></v-text-field>
        <v-btn
          class="primary"
          elevation="1"
          small
          :disabled="isAddColumnDisabled"
          @click="addColumn"
        >
          <v-icon>mdi-plus</v-icon>
        </v-btn>
      </v-card-actions>

      <!--      TODO - currently hiding formula / operation select-->
      <!--      <v-card-actions class="justify-center pt-0 pl-2 pr-2">-->
      <!--        <v-btn-->
      <!--          x-small-->
      <!--          @click="toggleAddColumnMode(modes.FORMULA)"-->
      <!--          :class="isActiveModeClass(modes.FORMULA)"-->
      <!--          >Formula</v-btn-->
      <!--        >-->
      <!--        <v-btn-->
      <!--          x-small-->
      <!--          @click="toggleAddColumnMode(modes.OPERATION)"-->
      <!--          :class="isActiveModeClass(modes.OPERATION)"-->
      <!--          >Operation</v-btn-->
      <!--        >-->
      <!--      </v-card-actions>-->

      <v-card-actions v-if="activeMode === modes.FORMULA">
        <v-text-field
          v-model="formulaStr"
          :rules="[parsingRule]"
          label="Formula"
          placeholder="A*B"
          @input="checkFormulaStr"
        ></v-text-field>
      </v-card-actions>

      <!--      parsedResult {{ parsedResult }}-->

      <v-card-actions v-if="activeMode === modes.OPERATION">
        <v-select
          v-model="activeOperation"
          :items="operations"
          return-object
          label="Operation"
        >
          <template v-slot:selection="{ item }">
            {{ item.text }}
            <v-spacer />
            <v-icon>{{ getMdiIcon(item.icon) }}</v-icon>
          </template>
          <template v-slot:item="{ item }">
            {{ item.text }}
            <v-spacer />
            <v-icon>{{ getMdiIcon(item.icon) }}</v-icon>
          </template>
        </v-select>
      </v-card-actions>
    </v-card>
  </v-expand-x-transition>
</template>

<script>
export default {
  props: ["exampleRow", "parser"],
  computed: {
    isAddColumnDisabled() {
      return !this.isPermittedToAddColumn || this.isHeaderFirstCharDigit;
    },
    isHeaderFirstCharDigit() {
      if (this.header.length > 0) {
        let isFirstCharNumber = /\d/.test(this.header.charAt(0));
        return isFirstCharNumber;
      } else {
        return false;
      }
    },
  },
  data: () => ({
    header: "",
    activeMode: "formula",
    modes: {
      OPERATION: "operation",
      FORMULA: "formula",
    },
    activeOperation: { text: "Multiply", value: "multiply", icon: "close" },
    operations: [
      { text: "Add", value: "add", icon: "plus" },
      { text: "Subtract", value: "subtract", icon: "minus" },
      { text: "Multiply", value: "multiply", icon: "close" },
      { text: "Divide", value: "divide", icon: "slash-forward" },
      { text: "Modulo", value: "modulo", icon: "percent" },
      { text: "Exponent", value: "Exponent", icon: "exponent" },
      { text: "Logarithm", value: "logarithm", icon: "square-root" },
    ],
    formulaStr: "",
    testStr: "",
    parsedResult: null,
    isPermittedToAddColumn: false,
    errors: {
      "#ERROR!": "General error",
      "#DIV/0!": "Divide by zero error",
      "#NAME?": "Not recognised function name or variable name",
      "#N/A": "A value is not available to a formula",
      "#NUM!": "Formula encountered an invalid number",
      "#VALUE!": "One of formulas arguments is of the wrong type",
    },
  }),
  methods: {
    toggleAddColumnMode(newMode) {
      this.activeMode = newMode;
    },
    isActiveModeClass(currMode) {
      return [
        {
          primary: currMode === this.activeMode,
        },
      ];
    },
    operationChanged(newOperation) {
      this.activeOperation.text = newOperation.text;
    },
    getMdiIcon(iconName) {
      return `mdi-${iconName}`;
    },
    parse(string) {
      return this.parser.parse(string);
    },
    checkFormulaStr() {
      // parse column names to example data to validate A / B / C ...
      this.testStr = this.formulaStr;
      for (const [key, data] of Object.entries(this.exampleRow)) {
        let match = this.testStr.match(key);
        if (match) {
          this.testStr = this.testStr.replace(key, data.value);
        }
      }

      let output = this.parse(this.testStr);
      if (output.result && output.result.toString().length > 0) {
        this.parsedResult = output.result;
        this.isPermittedToAddColumn = true;
      } else {
        this.isPermittedToAddColumn = false;
        // console.error(this.handleParsingError(output.error));
      }
    },
    parsingRule() {
      let output = this.parse(this.testStr);
      if (output && output.error) {
        // console.log("Error msg: ", this.errors[output.error]);
        return this.errors[output.error];
      } else {
        return true;
      }
    },
    handleParsingError(errorKey) {
      return {
        error: errorKey,
        msg: this.errors[errorKey],
      };
    },
    addColumn() {
      // console.log("adding new column formulaStr- ", this.formulaStr);
      let payload = {
        header: {},
        formula: {},
      };

      if (this.header.length === 0) {
        payload.header.text = this.formulaStr;
        payload.header.value = this.formulaStr;
        payload.formula.key = this.formulaStr;
      } else {
        payload.header.text = this.header;
        payload.header.value = this.header.toLowerCase().replace(" ", "");
        payload.formula.key = this.header.toLowerCase().replace(" ", "");
      }

      payload.formula.value = this.formulaStr;
      this.$emit("addColumn", payload);
      this.resetInputs();
    },
    noFirstDigitRule() {
      let firstChar = this.header.charAt(0);
      let firstCharIsNumber = /\d/.test(firstChar);
      if (this.header.length > 0 && firstCharIsNumber) {
        return "Column name first char can not be a number";
      } else {
        return true;
      }
    },
    resetInputs() {
      this.header = "";
      this.formulaStr = "";
      this.testStr = "";
      this.parsedResult = null;
      this.isPermittedToAddColumn = false;
    },
  },
};
</script>
